/*config*/

body{font-family:'Open Sans', sans-serif}
body{font-size: 18px}
body{color:#323E4E}
body{font-weight:400}
body{font-style:normal}



/* End config */
 
header {position: relative; display:inline-block; 
width:100%; background:#323E4E; color:white;height:120px; padding:10px}

/* Logo */

header .logo {
position:absolute; 
font-size:40px;
top:25%; 
left:25px; 
color:white;}

/* Nav */
nav {
position: absolute;
display: block;
top: 0px;
right:0px;
width:20%;
}

/* Header Picture Set  */
.picture {
	position: absolute;
	left: 300px;
}

/* LI when hovering  */

li.a:hover {
	background-color:#8bb2c6;
	color: white;
	text-decoration:none;
}

/* Anchor Tag */

a {
	color: white; 
	cursor: pointer;
}

/* Anchor Tag when hovering  */
a:hover {
	background: white;
	color: black;
}

/* Anchor Tag Active */
a.active { background-color: #8bb2c6; color: white;}

/* UL Lists */
ul {list-style-type: none; margin: 0; padding: 0; background-color:#BED4DF;}

/* Anchor Tags */
a{color:#323E4E; text-decoration: underline; display:block;}

/* Container Fluid Padding */
.container-fluid{ padding: 20px;}


/* Padding for Scoot in Links */
.scoot {
	color:#323E4E; text-decoration: underline;
	padding:20px;
}

/* Blockquote in HomePage */
blockquote{
	margin: 20px;
	line-height: 1.5;
	padding:10px;
	background-color: #BED4DF;
	letter-spacing: .25px;
	text-line: center;
}

/* h2 */
h2{
	height:40px;
	font-size: 30px;
	margin-bottom:20px;
	
}

/* h1 */
h1{
	height: 35px;	
	font-size: 22px;
	margin-bottom:20px;
}


/* links in Links */
.links{
font-size:25px;
padding-left:40px;
text-align: center;
}

/* links_scoot in Links */
.links_scoot{
text-align: left;
}

/* Footerholder in Footer */
.footerHolder { 
position:fixed;
bottom: 0;
width: 100%;
text-align: center; 
background: #323E4E;
color:white;
margin-top:auto;
}

/* Anchor Tag Details */
nav ul li a {
	display:block;
	text-align: center;
	text-decoration:none;
	font-size:20px;
	letter-spacing:.25px;
}

/* About div in About */
.about{
display: flex;

}

/* AboutLeft in About, Text */
.aboutLeft{
	flex:1;
	float:left;
	width:50%;
}

/* About Right in About, Picture */
.aboutRight{ 
float:right;
margin-top:45px;
margin-bottom: 50px;

}

/* AboutPara in About, Text in AboutLeft */
.aboutPara{
padding-left:20px;


}

/* UTD picture in About, aboutRight */
.utdTeamPicture{
	text-align: center;
}


